import * as React from 'react';
import { APICallSearch } from 'Components/UI/SearchBar/APICallSearch';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons';
// import { InputLabel } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid } from '@material-ui/core';
import { getRolesUsersLists } from 'Pages/API/Calls/Roles';


const useStyles = makeStyles(() => ({
    selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px'
    },
    xIcon: {
        fontSize: 16
    }
}));
console.log(useStyles);

interface IAddUserRoleBodyProps {
    apiCallFields: any;

    selectedUsers: any[];
    setSelectedUsers: (data: any) => void;

    refreshUsersWithRole: boolean;
    usersWithRole: any[];
    setUsersWithRole: (data: any) => void;

    roleId: string;
    onClose: () => void;
    onSave: (items) => void;
}

interface IAddUserRoleBodyState {
    selectedUsers: any[];
    filter: any;
}

export class AddUserRoleBody extends React.Component<IAddUserRoleBodyProps, IAddUserRoleBodyState> {
    constructor(props) {
        super(props);
        this.state = {
            selectedUsers: [],
            filter: {
                fields: this.props.apiCallFields,
                where: {
                    and: [{}],
                    or: [
                        { 'roles.role_id': this.props.roleId }
                    ]
                }
            },
        }
    }

    componentDidUpdate(prevProps) {
        if (prevProps.selectedUsers !== this.props.selectedUsers)
            this.setState({ selectedUsers: this.props.selectedUsers });
        if (prevProps.refreshUsersWithRole !== this.props.refreshUsersWithRole)
            this.initializeData();
    }

    componentDidMount() {
        this.initializeData();
    }

    removeDuplicates(array: any[]) {
        let a: any[] = []
        array.map(x => {
            if (!a.some(b => b.role_id === x.role_id)) {
                a.push(x)
            }
        })
        return a
    };

    initializeData = () => {
        getRolesUsersLists(this.state.filter, '').then(resp => {
            let resp_body = resp.body.map(item => {
                item.roles = this.removeDuplicates(item.roles);
                return {
                    ...item, name: item.full_name ? item.full_name : item.first_name + " " + item.middle_name + " " + item.last_name,
                }
            });
            // this.props.setSelectedUsers(resp_body);
            this.props.setUsersWithRole(resp_body);
        }).catch(e => {
            // this.props.setSelectedUsers([]);
            console.log(e);
        });
    }


    handleOnSearch = (list) => {
        if (!list || typeof list !== 'object' || (list && !list.name) || (list && !list.username)) return;
        let arr = this.props.selectedUsers;
        arr.push(list);
        this.props.setSelectedUsers(arr);
        console.log(arr);
    }

    handleRemoveItem = (index) => {
        let arr = this.props.selectedUsers;
        arr.splice(index, 1);
        this.props.setSelectedUsers(arr);
    }

    handleOnSaveItems = () => {
        this.props.onSave(this.props.selectedUsers);
    }

    public render(): JSX.Element {
        const classes = {
            buttonsContainer: {
                padding: '1em',
            },
            selectedContainer: {
                padding: '3em 0 2em'
            },
            selectedItems: {
                borderRadius: 20,
                fontSize: 13,
                backgroundColor: '#d1d4db',
                padding: '5px 10px',
                margin: '5px',
                display: 'inline-block',
            },
            selectedText: {
                paddingRight: '5px'
            },
            saveButton: {
                width: '135px',
                fontSize: '14px',
                color: '#fff',
            },
            cancelButton: {
                width: '135px',
                fontSize: '14px',
            }
        };

        return (
            <>
                <div style={{ minHeight: 400, minWidth: 400 }}>
                    <APICallSearch
                        placeholder={'Please enter at least 3 characters to search.'}
                        fields={this.props.apiCallFields}
                        searchLabel={'Search user account'}
                        onSearch={this.handleOnSearch}
                        selectedUsers={this.state.selectedUsers}
                        usersWithRole={this.props.usersWithRole}
                    />
                    <div style={classes.selectedContainer}>
                        <div>
                            {this.state.selectedUsers.length > 0 ? <label>Selected Users</label> : null}
                        </div>
                        {this.state.selectedUsers.map((item, index) =>
                            <div style={classes.selectedItems}>
                                <span style={classes.selectedText}>{item.name}</span>
                                <span onClick={() => this.handleRemoveItem(index)}>
                                    <FontAwesomeIcon icon={faTimesCircle} />
                                </span>
                            </div>
                        )
                        }
                    </div>
                </div>
                <Grid container spacing={3} justify="center" alignItems="baseline">
                    <Grid item xs={4} style={{ paddingRight: 20 }}>
                        <Button style={classes.cancelButton} variant="outlined" color="secondary" onClick={() => this.props.onClose()}>Cancel</Button>
                    </Grid>
                    <Grid item xs={4} style={{ paddingLeft: 20 }}>
                        <Button disabled={this.state.selectedUsers.length < 1 ? true : false} style={classes.saveButton} variant="contained" color="primary" onClick={() => this.handleOnSaveItems()}>Save</Button>
                    </Grid>
                </Grid>
            </>
        )
    }

}
