import * as React from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faSearch,
} from '@fortawesome/free-solid-svg-icons';
import { getRolesUsersLists } from 'Pages/API/Calls/Roles';

export interface IAPICallSearchProps {
    fields: any;
    usersWithRole: any[];
    selectedUsers: any;
    placeholder?: string;
    searchLabel?: string;
    onSearch: (event) => void;
    onChange?: (value) => void;
}

const useStyles = makeStyles(theme => ({
    gridContainerNoPadding: {
        padding: theme.spacing(1),
    },
    searchIcon: {
        position: "absolute",
        left: "15px",
        top: "50%",
        transform: "translateY(-50%)",
        fontSize: "20px",
        color: "#b0b0b0"
    },
    searchContainer: {
        borderRadius: "3px",
        position: "relative",
    },
    searchBox: {
        display: "block",
        padding: "12px 12px 12px 40px",
        borderRadius: "3px",
        fontSize: "14px",
        width: "100%",
        border: '1px solid #c4c4c4',
        outline: 'none',
        overflow: "hidden",
        '&:hover': {
            border: "1px solid rgb(39, 46, 76)",
        },
        '&:focus': {
            outline: "2px solid #3AB77D"
        }
    },
    searchLabel: {
        fontSize: 14,
        display: 'block',
        padding: '5px 0',
    },
    comboBoxContainer: {
        opacity: 100,
        position: 'absolute',
        top: '100%',
        left: '0%',
        width: '100%',
        backgroundColor: '#fff',
        borderRadius: '2px',
        border: '1px solid #c4c4c4',
        padding: '10px',
        maxHeight: 300,
        overflowY: 'auto',
        zIndex: 1000
    },
    list: {
        fontSize: 12,
        padding: '5px',
        '&:hover': {
            backgroundColor: '#dddfe3',
        }
    }
}));


export const APICallSearch: React.FC<IAPICallSearchProps> = (props: IAPICallSearchProps): JSX.Element => {
    const {
        placeholder,
        searchLabel,
        onSearch,
        onChange,
    } = props;

    const classes = useStyles();
    const [newComboList, setNewComboList] = React.useState<any>(null);
    const [searchValue, setSearchValue] = React.useState<any>(null);
    const [typingTimeout, setTypingTimeout] = React.useState<any>(0);
    const [isLoadingChoices, setIsLoadingChoices] = React.useState<boolean>(false);
    const [ref, setRef] = React.useState<any>();
    const [typing, setTyping] = React.useState<boolean>(false);

    React.useEffect(() => {
        setRef(React.createRef());
    }, []);

    const removeDuplicateRoles = (array: any[]) => {
        let a: any[] = []
        array.map(x => {
            if (!a.some(b => b.role_id === x.role_id)) {
                a.push(x)
            }
        })
        return a
    };

    const getChoices = () => {

        const value = searchValue;
        console.log('SEARCH VAL', value);
        setIsLoadingChoices(true);
        const selectedUsernames = props.selectedUsers.map(item => { return item.username });
        let usernames: any[] = selectedUsernames;
        if (props.usersWithRole && props.usersWithRole.length > 0) {
            const usernamesWithRole = props.usersWithRole.map(item => { return item.username });
            usernames = [...usernames, ...usernamesWithRole];
        }
        getRolesUsersLists({ fields: props.fields, where: { username: { nin: usernames } } }, value).then(resp => {
            const resp_body = resp.body.map(item => {
                item.roles = removeDuplicateRoles(item.roles);
                return {
                    ...item, name: item.full_name ? item.full_name : item.first_name + " " + item.middle_name + " " + item.last_name,
                }
            });
            setNewComboList(resp_body)
            setIsLoadingChoices(false);
        }).catch(e => {
            setNewComboList([])
            setIsLoadingChoices(false);
            console.log(e);
        })
    }


    const handleKeyPress = (event) => {
        let value = event.target.value;
        onChange && onChange(value)


        if (!value) setNewComboList(null);
    }

    const searchCombo = (list) => {
        ref.current.focus();
        // setSearchValue(list);
        setNewComboList(null);
        onSearch(list);
        setSearchValue({ name: '' });
        setNewComboList(null);
    }

    React.useEffect(() => {
        if (!searchValue) return;
        setTyping(true);
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }
        setTypingTimeout(setTimeout(function () {
            if (searchValue.length >= 3) {
                console.log('timeout search');
                setTyping(false);
                getChoices();
            }

        }, 2000));
    }, [searchValue])


    return (
        <>
            <Grid container xs={12} style={{ padding: "10px 0" }}>
                <Grid item xs={12}>
                    {searchLabel ? <label className={classes.searchLabel}>{searchLabel}</label> : null}
                </Grid>
                <Grid item xs={12} className={classes.searchContainer} >
                    <FontAwesomeIcon icon={faSearch} className={classes.searchIcon} />
                    <input
                        ref={ref}
                        type={'search'}
                        placeholder={placeholder}
                        value={searchValue && searchValue.name !== undefined ? searchValue.name : null}
                        onChange={(event) => {
                            handleKeyPress(event)
                            setSearchValue(event.target.value)
                        }}
                        onKeyUp={(event) => {
                            let code = event.keyCode || event.which
                            if (code == 13 && typing && searchValue && searchValue.length >= 3) {
                                console.log('enter search');
                                if (typingTimeout) {
                                    clearTimeout(typingTimeout);
                                }
                                getChoices();
                                setTyping(false);

                            }
                        }}
                        className={classes.searchBox}
                    />
                    {isLoadingChoices ? (
                        <div className={classes.comboBoxContainer}>Loading...</div>
                    ) : newComboList && newComboList.length > 0 ?
                            <div className={classes.comboBoxContainer}>
                                <ul style={{ padding: 0, margin: 0, listStyle: 'none' }}>
                                    {
                                        newComboList && newComboList.map(list => (
                                            <li className={classes.list} onClick={() => searchCombo(list)}>{list && list.name ? list.name : null}</li>
                                        ))
                                    }
                                </ul>
                            </div>
                            : newComboList && newComboList.length === 0 ? (
                                <div className={classes.comboBoxContainer}>No Results</div>
                            ) : null}
                </Grid>
            </Grid>
        </>
    )
}