import * as React from 'react';
import { Typography, Grid, Link, Button } from '@material-ui/core';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { ReactTable } from 'Components/UI/TableComponent/ReactTable';
import { useState } from 'react';
import classNames from 'classnames';
import { SideModalBlock } from 'Components/UI';
import { AddUserRoleBody } from './AddUserRoleBody';
import { AddGroupRoleBody } from './AddGroupRoleBody';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';

import {
    getRolesUsersLists,
    attachUserRoles,
    detachUserRoles
} from 'Pages/API/Calls/Roles';
import moment from 'moment';
import { attachRoleToGroups } from 'Pages/API/Calls/Roles';
import { getAllGroupsAttachToRole } from 'Pages/API/Calls/Roles';
import { UI } from 'Components';
// import { TableComponent } from './TableComponent'



const user_fields = {
    _id: true,
    first_name: true,
    last_name: true,
    middle_name: true,
    username: true,
    employee_id: true,
    department: true,
    main_module: true,
    main_role: true,
    group: true,
    group_name: true,
    reports_to: true,
    roles: true,
    full_name: true,
    last_login: true,
    status: true
}

export interface IGroupsAndUsersProps {
    rows?: any,
    viewOnly: boolean,
    classes: any,
    openSideModalBlock?: boolean,
}

export const GroupsAndUsers: React.FC<IGroupsAndUsersProps> = (props: IGroupsAndUsersProps): JSX.Element => {
    const {
        rows,
        classes,
    } = props;
    const [activeItem, setActiveItem] = useState('Groups');
    const [activeClass, setActiveClass] = useState(0);
    const [groupShowBlock, setGroupShowBlock] = useState('none');
    const [userShowBlock, setUserShowBlock] = useState('none');
    const [isFetching, setIsFetching] = React.useState(false);
    const [typingTimeout, setTypingTimeout] = React.useState<any>(0)
    const [userList, setUserList] = useState<any>();
    const [userTable, setUserTable] = useState<any>({
        limit: 10,
        skip: 0,
        currentPage: 0,
        order: [],
        filter: [],
        count: 0
    })

    // const [statuses, setStatuses] = React.useState<any>()
    const [tableStates, setTableState] = React.useState<any>({
        rows: [],
        currentPage: 0,
        totalCount: 0,
        pageSize: 10,
        offset: 0,
        order: '',
        where: {},
        query: ''
    })
    const [modalProps, setModalProps] = React.useState({
        title: '',
        message: '',
        open: false,
        method: () => { }
    });
    const [confirmationModalProps, setConfirmationModalProps] = React.useState({
        title: '',
        message: '',
        open: false,
        method: () => { }
    });
    const [selectedUsers, setSelectedUsers] = useState<any[]>([]);
    const [usersWithRole, setUsersWithRole] = useState<any[]>([]);
    const [refreshUsers, setRefreshUsers] = useState<boolean>(false);
    const [isConfirmDetachUserRoleModalOpen, setIsConfirmDetachUserRoleModalOpen] = useState<boolean>(false);
    const [userDetachRole, setUserDetachRole] = useState<any>(null);
    const [attachUserRoleNotifModalOpen, setAttachUserRoleNotifModalOpen] = useState<boolean>(false);
    const [attachUserRoleSuccess, setAttachUserRoleSuccess] = useState<boolean>(false);


    const userColumns: any = [
        {
            name: 'username',
            title: 'Username',
        },
        {
            name: 'full_name',
            title: 'User Account',
        },
        {
            name: 'last_login',
            title: 'Last Activity Date',
            getCellValue: row => (row.last_login ? moment(row.last_login).format('MMMM DD, YYYY') : 'N/A')
        },
        {
            name: 'status',
            title: 'Status',
            getCellValue: row => (row.status ? row.status.charAt(0).toUpperCase() + row.status.slice(1) : '')
        },
        {
            name: 'action_delete',
            title: ' ',
        },
    ]

    const groupsColumns: any = [
        {
            name: 'group_id',
            title: 'Group ID',
        },
        {
            name: 'name',
            title: 'Group',
        },
        {
            name: 'user_count',
            title: 'Users in the Group',
        },
        {
            name: 'status',
            title: 'Status',
        },
        {
            name: 'action_delete',
            title: ' ',
        },
    ];

    const menuItems = [
        'Groups',
        'Users'
    ]





    React.useEffect(() => {
        // groups list endpoint
        console.log(rows)
        APIGetGroupsAttachToRoleList({
            pageSize: 10,
            offset: 0,
            role_id: `${rows["role_id"]}`,
            where: {},
            order: ["group_id ASC"]
        }, true);
    }, []);



    const handleMenuClick = (item, index) => {
        setActiveItem(item);
        setActiveClass(index)
        if (item == 'Users') {
            getUserLists(
                10,
                0,
                [],
                0,
                [],
            )
        }
        if (item == 'Groups') {
            APIGetGroupsAttachToRoleList({
                pageSize: 10,
                offset: 0,
                role_id: `${rows["role_id"]}`,
                where: {},
                order: ["group_id ASC"]
            }, true);
        }
    }


    const removeDuplicateRoles = (array: any[]) => {
        let a: any[] = []
        array.map(x => {
            if (!a.some(b => b.role_id === x.role_id)) {
                a.push(x)
            }
        })
        return a
    };



    const attachUserRole = async (data: any) => {
        setIsFetching(true);
        await attachUserRoles(rows.role_id, data).then(resp => {
            console.log(resp)
            setAttachUserRoleSuccess(true);
        }).catch(e => {
            setAttachUserRoleSuccess(false);
            console.log(e)
        });
        setAttachUserRoleNotifModalOpen(true);
        setSelectedUsers([]);
        setUserShowBlock('none');
        getUserLists(
            userTable.limit,
            userTable.skip,
            userTable.order,
            userTable.currentPage,
            userTable.filter,
        );
        setRefreshUsers(!refreshUsers);
    }

    const detachUserRole = async (data: any) => {
        setIsConfirmDetachUserRoleModalOpen(false);
        setIsFetching(true);
        await detachUserRoles(rows.role_id, data).then(resp => console.log(resp)).catch(e => console.log(e));
        setUserShowBlock('none');
        getUserLists(
            userTable.limit,
            userTable.skip,
            userTable.order,
            userTable.currentPage,
            userTable.filter,
        );
        setRefreshUsers(!refreshUsers);
    }


    const getUserLists = (
        limit: number,
        skip: number,
        order: any,
        page: number,
        filter: any,
        search?: string
    ) => {

        if (typingTimeout) {
            clearTimeout(typingTimeout)
        }

        let _filter: any = {};
        let _order = order.length > 0 ? `${order[0].columnName === 'full_name' ? 'first_name' : order[0].columnName} ${order[0].direction}` : [];

        if (filter.length > 0) {
            filter.map((item: any) => {
                if (item.columnName == 'status') {
                    if (item.value !== 'all') {
                        _filter[item.columnName] = item.value
                    }
                } else if (item.columnName == 'full_name') {
                    _filter['first_name'] = {
                        like: `^${item.value}.*`,
                        options: "i"
                    }
                } else if (item.columnName == 'last_login') {
                    _filter['and'] = [
                        { last_login: { lte: `${moment(item.value).endOf('day').toISOString()}` } },
                        { last_login: { gte: `${moment(item.value).startOf('day').toISOString()}` } }
                    ]
                    // _filter['last_login'] = {
                    //     lte: `${moment(item.value).toISOString()}`
                    // }
                } else {
                    _filter[item.columnName] = {
                        like: `^${item.value}.*`,
                        options: "i"
                    }
                }
            })
        }

        let params = {
            fields: user_fields,
            limit: limit,
            offset: skip,
            order: _order,
            where: {
                "and": [
                    {
                        "roles.role_id": rows.role_id,
                    },
                    {
                        "or": [_filter]
                    }
                ],
            }
        }

        setTypingTimeout(
            setTimeout(() => {
                // setLoading(true)
                setIsFetching(true)
                getRolesUsersLists(params, search ? search : '').then((response) => {
                    const resp_body = response.body.map(item => {
                        item.roles = removeDuplicateRoles(item.roles);
                        return item;
                    });
                    setUserList(resp_body)
                    setUserTable({
                        currentPage: userTable.currentPage > (Math.ceil(response.count / limit) - 1) ? (Math.ceil(response.count / limit) - 1) : page,
                        order: order,
                        limit: limit,
                        skip: skip,
                        filter: filter,
                        count: response.count
                    })
                    // setLoading(false)
                    setIsFetching(false)
                    console.log(resp_body)
                }).catch(err => {
                    // alert(err);
                    setUserList([])
                    console.log(err)
                    setIsFetching(false)
                    // setLoading(false)
                })
            }, filter.length > 0 || search ? 2000 : 0)
        )
    }



    const APIGetGroupsAttachToRoleList = (newState?: any, saveSummary?: boolean) => {
        setIsFetching(true)


        getAllGroupsAttachToRole(
            newState.role_id,
            newState.pageSize,
            newState.offset,
            newState.where,
            newState.order,
            newState.query).then((data: any) => {
                console.log(data)
                if (!data.error) {

                    const state = {
                        ...newState,
                        rows: data.body,
                        totalCount: data.count.count
                    }


                    if (saveSummary && saveSummary === true) {
                        // setStatuses(data.statuses)
                    }

                    updateTableState(state)


                } else {
                    const state = {
                        ...newState,
                        rows: [],
                        totalCount: 0
                    }

                    updateTableState(state)
                }

                setIsFetching(false)
            }).catch(() => {
                console.log("catch")
                setIsFetching(false)
                const state = {
                    ...newState,
                    rows: [],
                    totalCount: 0
                }

                updateTableState(state)
            })
    }




    const updateTableState = (prop) => {
        setTableState({
            ...tableStates,
            ...prop
        })
    }


    const onSaveAddRoleToGroup = (items) => {
        // console.log(items)
        setIsFetching(true)
        let data = {
            body: items
        }
        let payload: any = [];

        if (tableStates && tableStates.length !== null) {
            payload.push(...tableStates.rows)
        }

        if (data.body && data.body.length > 0) {
            data.body.map(c => {
                let selectedGroupId = {
                    _id: c["id"],
                    group_id: c["group_id"],
                    name: c["name"],
                    description: c["description"],
                    user_account: c["user_count"],
                    effectivity_date: c["effectivity_date"],
                    date_updated: c["date_updated"],
                    is_processmaker: c["is_processmaker"],
                    pmaker_uid: c["pmaker_uid"],
                    amount_limit: c["amount_limit"],
                    user_count: c["user_count"],
                    status: c["status"],
                    department: c["department"]
                }
                let groupRoles = c["roles"]

                if (groupRoles && groupRoles.length > 0) {
                    var isUnique = true;
                    groupRoles.map(e => {
                        var role_id = e["_id"];
                        if (role_id === rows["_id"]) {
                            isUnique = false;
                        }
                    })
                    if (isUnique === true) {
                        groupRoles.push(rows)
                    }
                } else {
                    groupRoles.push(rows)
                }

                let expiry_date = c["expiry_date"]
                let rank = c["rank"]
                let reports_to = c["reports_to"]
                let meta_data = c["meta_data"]

                if (expiry_date !== undefined) { selectedGroupId["expiry_date"] = expiry_date }
                if (rank !== undefined) { selectedGroupId["rank"] = rank }
                if (reports_to !== undefined) { selectedGroupId["reports_to"] = reports_to }
                if (meta_data !== undefined) { selectedGroupId["meta_data"] = meta_data }

                selectedGroupId["roles"] = groupRoles;



                payload.push(selectedGroupId)
            })
        }
        console.log(payload)
        attachRoleToGroups(rows["role_id"], payload, '').then((resp: any) => {
            setIsFetching(false)
            if (!resp.error) {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'User group added!',
                    message: 'New user group have been successfully added.',
                    method: handleCloseModalProps
                })
            } else {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Add User Group Failed!',
                    message: 'Something went wrong. New user wasnt added.',
                    method: handleCloseModalProps
                })
            }
        }).catch((err) => {
            setIsFetching(false)
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Add User Group Failed!',
                message: err.message,
                method: handleCloseModalProps
            })
            console.log(err)
        })
        console.log(JSON.stringify(payload));
    }

    const handleDeleteGroup = (roledata) => {
        handleCloseConfirmationModalProps();
        setIsFetching(true)
        let data = {
            body: [roledata],
            label: "name"
        }

        let payload: any = [];

        if (data.body && data.body.length > 0) {
            data.body.map(a => {
                let selectedGroupToRemove = {
                    _id: a["_id"],
                    group_id: a["group_id"],
                    name: a["name"],
                    description: a["description"],
                    user_account: a["user_count"],
                    effectivity_date: a["effectivity_date"],
                    date_updated: a["date_updated"],
                    is_processmaker: a["is_processmaker"],
                    pmaker_uid: a["pmaker_uid"],
                    amount_limit: a["amount_limit"],
                    user_count: a["user_count"],
                    department: a["department"],
                    status: a.status.props.children
                }
                let groupRoles = a["roles"]
                let newGroupRoles: any = [];

                if (groupRoles && groupRoles.length > 0) {

                    groupRoles.map(e => {
                        var role_id = e["_id"];
                        if (role_id === rows["_id"]) {
                            newGroupRoles.push(e)
                        }
                    })
                }

                let expiry_date = a["expiry_date"]
                let rank = a["rank"]
                let reports_to = a["reports_to"]
                let meta_data = a["meta_data"]

                if (expiry_date !== undefined) { selectedGroupToRemove["expiry_date"] = expiry_date }
                if (rank !== undefined) { selectedGroupToRemove["rank"] = rank }
                if (reports_to !== undefined) { selectedGroupToRemove["reports_to"] = reports_to }
                if (meta_data !== undefined) { selectedGroupToRemove["meta_data"] = meta_data }

                selectedGroupToRemove["roles"] = newGroupRoles;
                payload.push(selectedGroupToRemove)
            })
        }
        console.log(payload)
        attachRoleToGroups(rows["role_id"], payload, 'remove').then((resp: any) => {
            setIsFetching(false)
            if (!resp.error) {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'User group removed!',
                    message: `${roledata["name"]} has been successfully removed.`,
                    method: handleCloseModalProps
                })
            } else {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Remove User Group Failed!',
                    message: 'Something went wrong. User group wasnt removed.',
                    method: handleCloseModalProps
                })
            }
        }).catch((err) => {
            setIsFetching(false)
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Removed User Group Failed!',
                message: err.message,
                method: handleCloseModalProps
            })
            console.log(err)
        })
        console.log(JSON.stringify(payload));

    }


    const rowDataFixer = (array: any) => {
        let newEntry: any = [];
        if (array.length > 0) {
            array.forEach(data => {
                let newRow = {
                    ...data,
                    group_id: `${data.group_id}`,
                    name: data.hasOwnProperty('name') ? data.name : 'N/A',
                    user_count: data.hasOwnProperty('user_count') ? data.user_count : 'N/A',
                    status: data.hasOwnProperty('status') ? <span style={{ textTransform: 'capitalize' }}>{data.status}</span> : 'N/A',
                }

                newEntry.push(newRow)
            })

            return newEntry
        }

        return []
    }

    const handleTimeOutGroupSearch = (value) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }

        setTypingTimeout(setTimeout(function () {
            const state = {
                currentPage: 0,
                pageSize: tableStates.pageSize,
                offset: 0,
                role_id: `${rows["role_id"]}`,
                order: tableStates.order ? tableStates.order : "group_id ASC",
                where: tableStates.where,
                query: value
            }

            APIGetGroupsAttachToRoleList(state)

        }, 3000));
    }


    const handleSearchGroup = (e) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }
        const state = {
            currentPage: 0,
            pageSize: tableStates.pageSize,
            offset: 0,
            role_id: `${rows["role_id"]}`,
            order: tableStates.order ? tableStates.order : "group_id ASC",
            where: tableStates.where,
            query: e
        }

        APIGetGroupsAttachToRoleList(state)

    }


    const onFilterChange = (value) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }

        setTypingTimeout(setTimeout(function () {
            const state = {
                currentPage: 0,
                pageSize: tableStates.pageSize,
                offset: 0,
                role_id: `${rows["role_id"]}`,
                order: tableStates.order ? tableStates.order : "group_id ASC",
                where: {},
                query: tableStates.query
            }

            value.map(w =>
                w.columnName === 'group_id' && w.value !== '' ?
                    state.where['group_id'] = parseInt(w.value)
                    : w.columnName === 'name' ?
                        state.where['name'] = {
                            like: `${w.value}.*`,
                            options: 'i'
                        }
                        : w.columnName === 'user_count' && w.value !== '' ?
                            state.where["user_count"] = parseInt(w.value)
                            : w.columnName === 'status' && w.value !== 'all' ?
                                state.where["status"] = w.value
                                : ''
            )

            console.log(state)
            APIGetGroupsAttachToRoleList(state)
        }, 3000));
    }

    const onCurrentPageChange = (value) => {
        const state = {
            pageSize: tableStates.pageSize,
            currentPage: value,
            role_id: `${rows["role_id"]}`,
            order: tableStates.order ? tableStates.order : "group_id ASC",
            offset: tableStates.pageSize * value,
            where: tableStates.where,
            query: tableStates.query
        }

        APIGetGroupsAttachToRoleList(state)
    }

    const onPageSizeChange = (value) => {
        const state = {
            pageSize: value,
            offset: tableStates.offset,
            role_id: `${rows["role_id"]}`,
            order: tableStates.order ? tableStates.order : "group_id ASC",
            where: tableStates.where,
            query: tableStates.query
        }

        if (tableStates.currentPage > (Math.ceil(tableStates.totalCount / value) - 1)) {
            state['currentPage'] = ((Math.ceil(tableStates.totalCount / value) - 1))
            state['offset'] = ((Math.ceil(tableStates.totalCount / value) - 1)) * value
        }

        APIGetGroupsAttachToRoleList(state)
    }

    const onSortingChange = (value) => {

        let filter = {
            ...value[0],
            direction: value[0].direction.toUpperCase()
        }

        let a = Object.values(filter).join(' ')

        const state = {
            pageSize: tableStates.pageSize,
            offset: tableStates.offset,
            role_id: `${rows["role_id"]}`,
            order: [a],
            where: tableStates.where,
            query: tableStates.query
        }

        APIGetGroupsAttachToRoleList(state)
    }


    const handleCloseModalProps = () => {
        setModalProps({
            ...modalProps,
            open: false
        })
        setGroupShowBlock('none')
        APIGetGroupsAttachToRoleList({
            pageSize: 10,
            offset: 0,
            role_id: `${rows["role_id"]}`,
            where: {},
            order: ["group_id ASC"]
        }, true);
    };

    const handleOpenConfirmationModalProps = (data) => {
        console.log("this is for deletion")
        console.log(data)
        setConfirmationModalProps({
            ...confirmationModalProps,
            open: true,
            title: "Remove from role",
            message: `Are you sure you want to remove ${data.name} from the role ${rows["name"]}?`,
            method: () => { handleDeleteGroup(data) }
        });
    }

    const handleCloseConfirmationModalProps = () => {
        setConfirmationModalProps({
            ...confirmationModalProps,
            open: false
        })
    };


    return (
        <>
            {isFetching && <UI.LoadingIndicator />}
            <Grid container xs={12}>
                <Grid item xs={12}>
                    <Typography><b>Attached Groups and Users</b></Typography>
                </Grid>
                <Grid item xs={12}>
                    <Grid container xs={12} alignItems={'center'}>
                        <Grid item xs={6}>
                            <ul style={{ padding: '0', margin: '0' }}>
                                {
                                    menuItems.map((item, i) => (
                                        <li className={classes.menuList}>
                                            <Link
                                                className={classNames(classes.menuLink, activeClass === i ? classes.LinkActive : null)}
                                                onClick={() => handleMenuClick(item, i)}>
                                                {item}
                                            </Link>
                                        </li>
                                    ))
                                }
                            </ul>
                        </Grid>
                        <Grid item xs={6} className={classes.alignRight}>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    {
                        activeItem == 'Groups' ?
                            <>
                                <Grid className={classes.alignRight}>
                                    <Link onClick={() => { setGroupShowBlock('block') }}> Add to this role</Link>
                                </Grid>
                                <ReactSearch
                                    onChange={handleTimeOutGroupSearch}
                                    placeholder={'Search a user group'}
                                    onSearch={handleSearchGroup} />
                                <ReactTable
                                    rows={rowDataFixer(tableStates.rows)}
                                    columns={groupsColumns}
                                    viewOnly={false}
                                    enableSelection={false}
                                    totalCount={tableStates.totalCount}
                                    currentPage={tableStates.currentPage}
                                    pageSize={tableStates.pageSize}
                                    columnExtensions={
                                        [{
                                            columnName: 'action_delete',
                                            width: 75,
                                            wordWrapEnabled: true,
                                            align: 'center'
                                        }]
                                    }
                                    message={'There are no groups to show yet.'}
                                    sortingStateColumnExtensions={[
                                        { columnName: 'action_delete', sortingEnabled: false }
                                    ]}
                                    onFilterChange={onFilterChange}
                                    onPageChange={onCurrentPageChange}
                                    onSetPageSize={onPageSizeChange}
                                    onSortingChange={onSortingChange}
                                    onDeleteRow={(row) => {
                                        handleOpenConfirmationModalProps(row)
                                    }}
                                />
                            </>
                            : null
                    }
                    {
                        activeItem == 'Users' ?
                            <>
                                <Grid className={classes.alignRight}>
                                    <Link onClick={() => { setUserShowBlock('block') }}> Add to this role</Link>
                                </Grid>
                                <ReactSearch
                                    onSearch={(search: string) => {
                                        getUserLists(
                                            userTable.limit,
                                            userTable.skip,
                                            userTable.order,
                                            userTable.currentPage,
                                            userTable.filter,
                                            search
                                        )
                                    }}
                                    onChange={(search: string) => {
                                        getUserLists(
                                            userTable.limit,
                                            userTable.skip,
                                            userTable.order,
                                            userTable.currentPage,
                                            userTable.filter,
                                            search
                                        )
                                    }}
                                    placeholder={'Search a user account'} />
                                <ReactTable
                                    rows={userList ? userList : []}
                                    columns={userColumns}
                                    columnExtensions={
                                        [{
                                            columnName: 'action_delete',
                                            width: 75,
                                            wordWrapEnabled: true,
                                            align: 'center'
                                        }]
                                    }
                                    message={'There are no user accounts to show yet.'}
                                    sortingStateColumnExtensions={[
                                        { columnName: 'action_delete', sortingEnabled: false }
                                    ]}
                                    onDeleteRow={(row) => {
                                        setUserDetachRole(row);
                                        setIsConfirmDetachUserRoleModalOpen(true);
                                        console.log(row, 'rowdeleted');
                                    }}
                                    currentPage={userTable.currentPage}
                                    pageSize={userTable.limit}
                                    totalCount={userTable.count}
                                    defaultFilters={userTable.filter}
                                    onFilterChange={(filter: any) => {
                                        getUserLists(
                                            userTable.limit,
                                            0,
                                            userTable.order,
                                            0,
                                            filter,
                                        )
                                    }}
                                    onSortingChange={(sort: any) => {
                                        getUserLists(
                                            userTable.limit,
                                            userTable.skip,
                                            sort,
                                            userTable.currentPage,
                                            userTable.filter,
                                        )
                                    }}
                                    onSetPageSize={(size: number) => {
                                        getUserLists(
                                            size,
                                            userTable.skip,
                                            userTable.order,
                                            userTable.currentPage,
                                            userTable.filter,
                                        )
                                    }}
                                    onPageChange={(page: number) => {
                                        let skip = page * userTable.limit
                                        getUserLists(
                                            userTable.limit,
                                            skip,
                                            userTable.order,
                                            page,
                                            userTable.filter,
                                        )
                                    }}

                                />
                            </>
                            : null
                    }
                </Grid>
            </Grid>

            <SideModalBlock
                handleClose={() => { setGroupShowBlock('none') }}
                showBlock={activeItem == 'Groups' ? groupShowBlock : 'none'}
                title={'Add to this Role'}>
                <AddGroupRoleBody
                    onClose={() => {
                        setGroupShowBlock('none')
                    }}
                    onSave={(data: any) => {
                        onSaveAddRoleToGroup(data)
                    }}
                />
            </SideModalBlock>

            <SideModalBlock
                handleClose={() => { setUserShowBlock('none') }}
                showBlock={activeItem == 'Users' ? userShowBlock : 'none'}
                title={'Add to this Role'}>
                <AddUserRoleBody
                    apiCallFields={user_fields}
                    roleId={rows.role_id}
                    refreshUsersWithRole={refreshUsers}
                    selectedUsers={selectedUsers}
                    setSelectedUsers={(data: any) => setSelectedUsers([...data])}
                    usersWithRole={usersWithRole}
                    setUsersWithRole={(data: any) => setUsersWithRole([...data])}
                    onClose={() => {
                        setUserShowBlock('none')
                    }}
                    onSave={(data: any) => {
                        if (!Array.isArray(data)) return;

                        const attach_role_data = data.filter(user => {
                            if (user && user.roles && user.roles.some(role => role.role_id === rows.role_id)) {
                                return false;
                            }
                            return true;
                        });
                        console.log('ATTACH DATA', attach_role_data);
                        attachUserRole(attach_role_data);
                    }}
                />
            </SideModalBlock>


            {/* {this modal is for success and error for both adding groups and accounts} */}
            <ModalComponent
                isModalOpen={modalProps.open}
                onClose={modalProps.method}
                message={modalProps.message}
                title={modalProps.title}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item >
                            <Button
                                onClick={modalProps.method}
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}>
                                Okay
                            </Button>
                        </Grid>
                    </Grid>
                }
            />

            {/* {this modal is for confirmation for both removing groups and accounts} */}
            <ModalComponent
                isModalOpen={confirmationModalProps.open}
                onClose={handleCloseConfirmationModalProps}
                message={confirmationModalProps.message}
                title={confirmationModalProps.title}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button
                                onClick={handleCloseConfirmationModalProps}
                                className={classes.button}
                                variant={'outlined'}
                                color={'secondary'}>
                                Cancel
                            </Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button
                                onClick={confirmationModalProps.method}
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}>
                                Remove
                            </Button>
                        </Grid>
                    </Grid>
                }
            />


            <ModalComponent
                isModalOpen={isConfirmDetachUserRoleModalOpen}
                onClose={() => {
                    setIsConfirmDetachUserRoleModalOpen(false);
                }}
                message={`Remove ${userDetachRole && userDetachRole.full_name ? userDetachRole.full_name : 'user'} from this role?`}
                title={'Remove from Role'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button onClick={() => { setIsConfirmDetachUserRoleModalOpen(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button className={classes.button} variant={'contained'} color={'primary'} onClick={() => detachUserRole([userDetachRole])}>Remove</Button>
                        </Grid>
                    </Grid>
                }
            />
            {/* To-do: apply success and fail messages. */}
            <ModalComponent
                isModalOpen={attachUserRoleNotifModalOpen}
                onClose={() => setAttachUserRoleNotifModalOpen(false)}
                message={attachUserRoleSuccess ? 'New user account/s have been successfully added.' : 'New user account/s have been successfully added.'}
                title={attachUserRoleSuccess ? 'User account added!' : 'User account added!'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button className={classes.button} variant={'contained'} color={'primary'} onClick={() => setAttachUserRoleNotifModalOpen(false)}>Okay</Button>
                        </Grid>
                    </Grid>
                }
            />
        </>
    )
}
