import * as React from 'react';
import { SearchUserGroup } from 'Components/UI/SearchBar/SearchUserGroup';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons';
// import { InputLabel } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid } from '@material-ui/core';
import { getAllGroups } from 'Pages/API/Calls/Roles';

const useStyles = makeStyles(() => ({
    selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px'
    },
    xIcon: {
        fontSize: 16
    }
}));
console.log(useStyles);

interface IAddGroupRoleBodyProps {
    onClose: () => void;
    onSave: (items) => void;
}

interface IAddGroupRoleBodyState {
    selectedItems: any;
    comboArray: any[];
}


export class AddGroupRoleBody extends React.Component<IAddGroupRoleBodyProps, IAddGroupRoleBodyState> {
    constructor(props) {
        super(props);
        this.state = {
            selectedItems: [],
            comboArray: [],
        }
    }

    componentDidMount() {
        getAllGroups().then(res => {
            console.log(res)
            let data = {
                body: res.body,
                id: "_id",
                group_id: "group_id",
                name: "name",
                description: "description",
                user_count: "user_count",
                effectivity_date: "effectivity_date",
                date_updated: "date_updated",
                is_processmaker: "is_processmaker",
                pmaker_uid: "pmaker_uid",
                amount_limit: "amount_limit",
                roles: "roles",
                status: "status",
                department: "department",
                expiry_date: "expiry_date",
                rank: "rank",
                reports_to: "reports_to",
                meta_data: "meta_data"
            }
            let newChoices: any = [];

            if (data.body && data.body.length > 0) {
                data.body.map(c => {
                    let beauty = {
                        // value: c,
                        id: c[data.id],
                        group_id: c[data.group_id],
                        name: c[data.name],
                        description: c[data.description],
                        user_count: c[data.user_count],
                        effectivity_date: c[data.effectivity_date],
                        date_updated: c[data.date_updated],
                        is_processmaker: c[data.is_processmaker],
                        pmaker_uid: c[data.pmaker_uid],
                        amount_limit: c[data.amount_limit],
                        roles: c[data.roles],
                        status: c[data.status],
                        department: c[data.department],
                        expiry_date: c[data.expiry_date] ? c[data.expiry_date] : undefined,
                        rank: c[data.rank] ? c[data.rank] : undefined,
                        reports_to: c[data.reports_to] ? c[data.reports_to] : undefined,
                        meta_data: c[data.meta_data] ? c[data.meta_data] : undefined
                    }
                    newChoices.push(beauty)
                })
                this.setState({
                    comboArray: newChoices
                })
                console.log(this.state.comboArray)
            }
        }).catch(err => {
            console.log(err);
        })
    }


    handleOnSearch = (list) => {
        console.log(list)
        if (!list || typeof list !== 'object' || (list && !list.name)) return;
        let arr = this.state.selectedItems;
        arr.push(list);
        this.setState({
            selectedItems: arr
        })
        console.log(arr);
    }

    handleRemoveItem = (index) => {
        let arr = this.state.selectedItems;
        arr.splice(index, 1);
        this.setState({
            selectedItems: arr
        })
    }

    handleOnSaveItems = () => {
        this.props.onSave(this.state.selectedItems);
        this.setState({
            selectedItems: []
        })
    }

    public render(): JSX.Element {
        const classes = {
            buttonsContainer: {
                padding: '1em',
            },
            selectedContainer: {
                padding: '3em 0 2em'
            },
            selectedItems: {
                borderRadius: 20,
                fontSize: 13,
                backgroundColor: '#d1d4db',
                padding: '5px 10px',
                margin: '5px',
                display: 'inline-block',
            },
            selectedText: {
                paddingRight: '5px'
            },
            saveButton: {
                width: '135px',
                fontSize: '14px',
                color: '#fff',
            },
            cancelButton: {
                width: '135px',
                fontSize: '14px',
            }
        };

        return (
            <>
                <div style={{ minHeight: 400, minWidth: 400 }}>
                    <SearchUserGroup
                        placeholder={'Please enter at least 3 characters to search.'}
                        searchLabel={'Search user group'}
                        onSearch={this.handleOnSearch}
                        comboBoxMode={true}
                        comboListsArray={this.state.comboArray}
                    />
                    <div style={classes.selectedContainer}>
                        {this.state.selectedItems.length > 0 ?
                            <div>
                                <label>Selected Groups</label>
                            </div>
                            : null}
                        {this.state.selectedItems.map((item, index) =>
                            <div style={classes.selectedItems}>
                                <span style={classes.selectedText}>{item.name}</span>
                                <span onClick={() => this.handleRemoveItem(index)}>
                                    <FontAwesomeIcon icon={faTimesCircle} />
                                </span>
                            </div>
                        )
                        }
                    </div>
                </div>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <Button style={classes.cancelButton} variant="outlined" color="secondary" onClick={() => this.props.onClose()}>Cancel</Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button disabled={this.state.selectedItems.length < 1 ? true : false}
                            style={classes.saveButton}
                            variant="contained"
                            color="primary"
                            onClick={() => this.handleOnSaveItems()}>
                            Add
                        </Button>
                    </Grid>
                </Grid>
            </>
        )
    }

}
